import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
//import { Customer } from '../interfaces/customer.model';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icClose from '@iconify/icons-ic/twotone-close';
import icPrint from '@iconify/icons-ic/twotone-print';
import icDownload from '@iconify/icons-ic/twotone-cloud-download';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icPhone from '@iconify/icons-ic/twotone-phone';
import icPerson from '@iconify/icons-ic/twotone-person';
import icMyLocation from '@iconify/icons-ic/twotone-my-location';
import icLocationCity from '@iconify/icons-ic/twotone-location-city';
import icEditLocation from '@iconify/icons-ic/twotone-edit-location';

import { NgForm } from '@angular/forms';
import { UnitsService } from '../../../../shared/services/units.services';
import { Units } from '../../../../shared/models/units';

import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from '@angular/fire/firestore';
import { firestore } from 'firebase';
import { StorageService } from '../../../../shared/services/storage/storage.service';

declare var toastr: any;
declare var $: any;
@Component({
  selector: 'vex-add-units',
  templateUrl: './add-units.component.html',
  styleUrls: ['./add-units.component.scss']
})
export class AddUnitsComponent implements OnInit {

  addUnits: Units = new Units();
  clientList: Units[];
  clientObject: Units;
  loading = false;

  static id = 1;

  form: FormGroup;
  mode: 'create' | 'update' = 'create';

  icMoreVert = icMoreVert;
  icClose = icClose;

  icPrint = icPrint;
  icDownload = icDownload;
  icDelete = icDelete;

  icPerson = icPerson;
  icMyLocation = icMyLocation;
  icLocationCity = icLocationCity;
  icEditLocation = icEditLocation;
  icPhone = icPhone;
  client: any;

  constructor(
    @Inject(MAT_DIALOG_DATA) 
    public defaults: any,
    private dialogRef: MatDialogRef<AddUnitsComponent>,
    private fb: FormBuilder,
    private firestore: AngularFirestore,
    private storageService: StorageService,
    private unitService: UnitsService
  ) { }

  ngOnInit() {
    if(this.storageService.getSession("$key")!=null){
      this.addUnits = this.storageService.getSession("mydata")     
    }

    if (this.defaults) {
      this.mode = 'update';
    } else {
      this.defaults = {} as Units;
    }

    const newId = this.firestore.createId();
    this.storageService.setSession("mykey", newId)

    this.form = this.fb.group({
      $key: newId,
      id: [AddUnitsComponent.id++],
      estado: [this.defaults.estado || ''],
      folioConcesion: [this.defaults.folioConcesion || ''],
      localidad: [this.defaults.localidad || ''],
      municipio: [this.defaults.municipio || ''],
      noUnidad: [this.defaults.noUnidad || ''],
      placa: [this.defaults.placa || ''],
      serieMotor: [this.defaults.serieMotor || ''],
    });
  }

  save(clientForm: NgForm) {
    if (this.mode === 'create') {
      this.addClient(clientForm);
    } else if (this.mode === 'update') {
      this.updateCustomers();
    }
  }

  addClient(clientForm: NgForm) {
    this.unitService.postUnits(clientForm.value);
    this.createCustomer();
    alert("cliente guardado")

  }

  updateCustomers(){
    if(this.storageService.getSession("$key")!=null){
      //var value = new AddEstablishment
        var values = { 
          estado: this.addUnits.estado,
          folioConcesion: this.addUnits.folioConcesion,
          localidad: this.addUnits.localidad,
          municipio: this.addUnits.municipio,
          noUnidad: this.addUnits.noUnidad,
          placa: this.addUnits.placa,
          serieMotor: this.addUnits.serieMotor,
        }
  
        const customer = this.form.value;  
        customer.estado = String(values.estado)
        customer.folioConcesion = Number(values.folioConcesion)
        customer.localidad = String(values.localidad)
        customer.municipio = String(values.municipio)
        customer.noUnidad = Number(values.noUnidad)
        customer.placa= String(values.placa)
        customer.serieMotor = Number(values.serieMotor)

        this.unitService.updateUnits(values)
        this.updateCustomer()
        console.log('print value::'+ JSON.stringify(customer));
      }else{
        console.log('no entro')
      }
  }

  createCustomer() {
    const customer = this.form.value;

    if (!customer.imageSrc) {
      customer.imageSrc = 'assets/img/avatars/1.jpg';
    }

    this.dialogRef.close(customer);
  }

  updateCustomer() {
    const customer = this.form.value;
    customer.id = this.defaults.id;

    this.dialogRef.close(customer);
  }

  isCreateMode() {
    return this.mode === 'create';
  }

  isUpdateMode() {
    return this.mode === 'update';
  }

}
